import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/index'
    },
    {
      path: '/',
      name: 'home',
      component: Home,
      redirect: '/index',
      children: [
        {
          path: 'index',
          name: 'index',
          component: () => import('./views/HomePage.vue')
        },
        {
          path: 'products',
          name: 'products',
          component: () => import('./views/Products.vue')
        },
        {
          path: 'login',
          name: 'login',
          component: () => import('./views/Login.vue')
        },
        {
          path: 'carts',
          name: 'carts',
          component: () => import('./views/Carts.vue')
        },
        {
          path: 'check',
          name: 'check',
          component: () => import('./views/Check.vue')
        },
        {
          path: 'checksuccess',
          name: 'checksuccess',
          component: () => import('./views/CheckSuccess.vue')
        }
      ]
    }
  ]
})
